.header {
   padding: 20px;
   width: 100%;
   position: fixed;
   top: 0;
   left: 0;
   user-select: none;
   z-index: 200;
   background: var(--black-t);
   transition: 0.3s;
   &__body {
      display: flex;
      flex-direction: column;
      gap: 30px;
      position: relative;
   }
   &__top {
      display: flex;
      justify-content: space-between;
   }
   &__logo {
      padding: 10px 20px;
      color: var(--black-p);
      background: var(--white-p);
      border-radius: 30px;
      font-weight: 700;
      white-space: nowrap;
      z-index: 5;
   }
   &__burger {
      display: none;
   }
}
.menu {
   display: flex;
   align-items: center;
   gap: 20px;
   &__list {
      display: flex;
      gap: 20px;
      z-index: 5;
   }
   &__item a {
      color: var(--white-p);
   }
}
.settings {
   z-index: 5;
   &__mode-btn {
      position: relative;
      min-width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--white-p);
      border-radius: 30px;
   }
   &__icon {
      color: var(--black-p);
      position: absolute;
      transition: 0.2s;
   }
   // change icon on click
   &__icon:nth-child(1) {
      opacity: 1;
   }
   &__icon:nth-child(2) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(1) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(2) {
      opacity: 1;
   }
}
.sidebar {
   white-space: nowrap;
   display: flex;
   justify-content: flex-end;
   opacity: 1;
   &__list-mobile {
      display: none;
   }
   &__list {
      display: flex;
      justify-content: flex-end;
      gap: 15px;
      text-align-last: left;
   }
   &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      cursor: pointer;
   }
   button {
      color: var(--white-p);
      position: relative;
   }
   &__arrow path {
      fill: var(--white-p);
   }
   &__arrow {
      width: 8px;
      height: 8px;
      transform: rotate(-45deg);
      transition: transform 0.3s;
   }
   &__arrow.open {
      transform: rotate(45deg);
   }
   &__item {
      position: relative;
   }
   &__inner {
      background: var(--black-p);
      border-radius: 20px;
      padding: 10px 20px;
   }
   &__dropdown {
      opacity: 0;
      visibility: hidden;
      max-height: 0;
      padding-top: 0px;
      transition: padding-top 0.3s, max-height 0.3s, opacity 0.3s,
         visibility 0.3s;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      gap: 5px;
   }
   &__dropdown.open {
      opacity: 1;
      padding-top: 10px;
      padding-bottom: 5px;
      visibility: visible;
      max-height: 150px;
   }
   a {
      color: var(--white-s);
   }
}
@media (max-width: $mobile) {
   .header {
      &__top {
         position: relative;
         z-index: 5;
      }
      &__burger {
         display: block;
         position: absolute;
         right: 50px;
         width: 40px;
         height: 40px;
         border-radius: 50%;
         color: var(--black-s);
         background: var(--white-p);
         z-index: 5;
      }
      &__icon-burger {
         position: relative;
         width: 100%;
         height: 100%;
         transition: 0.15s;
      }
      &__icon {
         position: absolute;
         height: 20px;
         width: 20px;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         color: var(--black-s);
      }
      &__icon:nth-child(1) {
         opacity: 1;
         transition: 0.3s;
      }
      &__icon:nth-child(2) {
         opacity: 0;
         transition: 0.3s;
      }
   }
   .sidebar {
      display: none;
      &__list-mobile {
         display: block;
         padding: 0;
         text-align: center;
         gap: 20px;
         display: flex;
         flex-direction: column;
         text-align: left;
         button {
            color: var(--white-p);
         }
         a {
            color: var(--white-s);
         }
      }
      &__inner {
         background: var(--black-p);
         padding: 0px;
         padding-right: 10px;
      }
      &__dropdown.open {
         opacity: 1;
         padding-top: 10px;
         padding-bottom: 0px;
         visibility: visible;
         max-height: 150px;
      }
   }
   .menu {
      &__list {
         position: fixed;
         opacity: 0;
         top: 0px;
         visibility: hidden;
         transition: 0.3s;
         left: 0;
         width: 100%;
         padding: 0px 20px;
         height: auto;
         z-index: 4;
         flex-direction: column;
         justify-content: center;
         align-content: center;
         text-align: left;
         background: var(--black-t);
      }
   }
   .menu-open {
      // burger animation
      .header {
         &__icon:nth-child(1) {
            opacity: 0;
            transition: 0.3s;
         }
         &__icon:nth-child(2) {
            opacity: 1;
            transition: 0.3s;
         }
      }
      .menu {
         &__list {
            padding: 20px 20px;
            padding-top: 80px;
            opacity: 1;
            visibility: visible;
         }
      }
   }
}
