.didactic {
   padding: 20px;
   &__title {
      padding-bottom: 10px;
   }
   &__grid {
      display: grid;
      grid-template-rows: repeat(1fr);
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      gap: 30px;
   }
}

.didactic-pers {
   background: var(--black-p);
   padding: 15px;
   border-radius: 25px;
   display: flex;
   flex-direction: column;
   gap: 10px;
   &__img {
      width: 100%;
      aspect-ratio: 1 / 1;
      position: relative;
      img {
         border-radius: 15px;
         transform-origin: top;
         position: absolute;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__grad {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 16px;
      height: 16px;
      background: var(--grad-color);
      z-index: 2;
      border-radius: 12px;
   }
   &__info {
      display: flex;
      justify-content: space-between;
      padding: 0 5px;
   }
   &__name {
   }
   &__exp {
   }
}
