.didactic-home {
   padding: 20px 20px 20px 20px;
   &__title {
      padding-bottom: 15px;
   }
   &__grid {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px;
      flex: 1 1 auto;
   }
}
.didactic-item {
   background: var(--black-p);
   border-radius: 25px;
   padding: 25px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   position: relative;
   gap: 25px;
   &::after {
      content: '';
      position: absolute;
      bottom: 25px;
      right: 25px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: var(--grad-color);
   }
   &__grad {
      display: flex;
      justify-content: space-between;
      font-size: 20px;
      line-height: 20px;
   }
   &__cadrs {
      font-size: 18px;
      line-height: 18px;
   }
}
.didactic-link {
   background: var(--white-p);
   border-radius: 25px;
   position: relative;
   &__body {
      padding: 25px;
      width: 100%;
      height: 100%;
   }
   &__name {
      font-size: 18px;
      color: var(--black-p);
      font-weight: bold;
   }
   &__icon {
      position: absolute;
      bottom: 20px;
      right: 20px;
      color: var(--black-p);
   }
}
@media (max-width: 1200px) {
   .didactic-item {
      aspect-ratio: unset;
   }
}
@media (max-width: $tablet) {
   .didactic-home {
      &__grid {
         gap: 15px;
         display: grid;
         grid-template-rows: 1fr 1fr 1fr;
         grid-template-columns: 1fr 1fr;
      }
   }
   .didactic-item {
      display: flex;
      flex-direction: column;
      gap: 25px;
   }
}
@media (max-width: $mobile) {
   .didactic-item {
      &::after {
         bottom: 25px;
         right: 25px;
         width: 16px;
         height: 16px;
      }
      &__grad {
         font-size: 18px;
         line-height: 18px;
      }
      svg {
         min-width: 18px;
         min-height: 18px;
         width: 18px;
         height: 18px;
      }
      &__cadrs {
         font-size: 16px;
         line-height: 16px;
      }
   }
   .didactic-link {
      &__name {
         font-size: 16px;
      }
   }
}
@media (max-width: $mobileSmall) {
   .didactic-item {
      &::after {
         width: 14px;
         height: 14px;
      }
      &__grad {
         font-size: 16px;
         line-height: 16px;
      }
      svg {
         min-width: 16px;
         min-height: 16px;
         width: 16px;
         height: 16px;
      }
      &__cadrs {
         font-size: 14px;
         line-height: 14px;
      }
   }
   .didactic-link {
      &__name {
         font-size: 16px;
      }
   }
}
