.intro {
   padding: 20px 20px 0px 20px;
   &__body {
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 0.7fr 1.3fr;
      gap: 30px;
   }
   &__left {
      aspect-ratio: 1 / 1;
      position: relative;
      overflow: hidden;
      background: #fff;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 20px 25px 25px 25px;
      border-radius: 25px;
      &::after {
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         background: linear-gradient(
            180deg,
            rgba(255, 255, 255, 0) 45.1%,
            rgba(255, 255, 255, 0.4) 63.85%,
            rgba(255, 255, 255, 0.86) 100%
         );
         z-index: 2;
      }
   }
   &__title {
      font-size: 30px;
      z-index: 3;
      color: #101010;
   }
   &__img {
      position: absolute;
      right: 0;
      width: 80%;
      height: 100%;
      object-fit: contain;
      z-index: 1;
      user-select: none;
   }
   &__description {
      z-index: 3;
      color: #101010;
      text-shadow: 0px 0px 2px #fff;
   }
   &__right {
      border-radius: 25px;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      gap: 20px;
      user-select: none;
      .intro__title {
         padding: 10px;
         color: var(--white-p);
      }
   }
   &__grid {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px;
      flex: 1 1 auto;
   }
}
.intro-item {
   background: var(--black-p);
   border-radius: 25px;
   display: flex;
   flex-direction: column;
   gap: 10px;
   position: relative;
   &:first-child {
      background: #52d778;
      .intro-item__name,
      .intro-item__icon,
      .intro-item__info {
         color: #101010;
      }
   }
   .intro-link__icon {
      color: var(--white-p);
   }
   &__body {
      padding: 25px;
      width: 100%;
      height: 100%;
   }
   &__name {
      font-size: 18px;
      font-weight: bold;
      white-space: nowrap;
   }
   &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 100%;
   }

   &__icon {
      min-height: 45px;
      min-width: 45px;
      color: var(--white-p);
   }

   &__info {
      width: 100%;
      text-align: center;
      font-size: 25px;
   }
}
.intro-link {
   background: var(--white-p);
   border-radius: 25px;
   position: relative;
   &__body {
      padding: 25px;
      width: 100%;
      height: 100%;
   }
   &__name {
      font-size: 18px;
      color: var(--black-p);
      font-weight: bold;
   }
   &__icon {
      position: absolute;
      bottom: 20px;
      right: 20px;
      color: var(--black-p);
   }
}
@media (max-width: 1200px) {
   .intro {
      padding: 20px;

      &__title {
         font-size: 25px;
         z-index: 3;
         color: #101010;
      }
      &__description {
         z-index: 3;
         color: #101010;
         text-shadow: 0px 0px 2px #fff;
         font-size: 12px;
      }
      &__grid {
         display: grid;
         grid-template-rows: 1fr 1fr;
         grid-template-columns: 1fr 1fr 1fr;
         gap: 20px;
         flex: 1 1 auto;
      }
   }
   .intro-item {
      gap: 10px;
      &__body {
         padding: 25px;
         display: flex;
         flex-direction: column;
         justify-content: space-between;
      }
      &__bottom {
         height: auto;
      }
      &__name {
         font-size: 16px;
      }
      &__icon {
         min-height: 25px;
         min-width: 25px;
      }
      &__info {
         font-size: 22px;
      }
   }
   .intro-link {
      background: var(--white-p);
      border-radius: 25px;
      position: relative;
      &__body {
         padding: 25px;
         width: 100%;
         height: 100%;
      }
      &__name {
         font-size: 16px;
         color: var(--black-s);
         font-weight: bold;
      }
      &__icon {
         position: absolute;
         bottom: 20px;
         right: 20px;
         color: var(--black-s);
      }
   }
}
@media (max-width: $tablet) {
   .intro {
      &__body {
         grid-template-rows: auto auto;
         grid-template-columns: 1fr;
      }
      &__left {
         aspect-ratio: 2 / 1;
      }
      &__description {
         z-index: 3;
         color: #101010;
         text-shadow: 0px 0px 2px #fff;
         font-size: 16px;
      }
      &__img {
         width: 50%;
         object-fit: cover;
      }
   }
}
@media (max-width: $mobile) {
   .intro {
      &__body {
         grid-template-rows: 1fr auto;
         grid-template-columns: 1fr;
      }
      &__left {
         aspect-ratio: 1 / 1;
      }
      &__img {
         width: 80%;
      }
      &__grid {
         gap: 15px;
         display: grid;
         grid-template-rows: 1fr 1fr 1fr;
         grid-template-columns: 1fr 1fr;
      }
   }
   .intro-item {
      &__body {
         padding: 25px;
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         gap: 25px;
      }
      &__name {
         font-size: 18px;
      }
      &__bottom {
         width: 100%;
         height: auto;
      }
      &__icon {
         min-height: 30px;
         min-width: 30px;
      }
      &__info {
         width: 100%;
         font-size: 20px;
      }
   }
   .intro-link {
      &__name {
         font-size: 16px;
      }
   }
}
@media (max-width: $mobileSmall) {
   .intro {
      &__body {
         gap: 20px;
      }
      &__title {
         font-size: 25px;
         z-index: 3;
         color: #101010;
      }
      &__description {
         z-index: 4;
         color: #101010;
         text-shadow: 0px 0px 2px #fff;
         font-size: 12px;
      }
   }
   .intro-item {
      &__body {
         gap: 20px;
      }
      &__name {
         font-size: 16px;
         font-weight: 500;
      }
      &__icon {
         min-height: 25px;
         min-width: 25px;
      }
      &__info {
         width: 100%;
         font-size: 14px;
      }
   }
   .intro-link {
      &__name {
         font-size: 14px;
      }
   }
}
@media (max-width: 375px) {
   .intro-item {
      &__name {
         font-size: 14px;
         font-weight: 500;
      }
   }
}
