.news {
   padding: 20px;
   &__title {
      padding: 0 20px 20px 20px;
   }
   &__grid {
      display: grid;
      grid-template-rows: repeat(1fr);
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      gap: 30px;
   }
}

.news-item {
   width: 100%;
   display: grid;
   grid-auto-flow: row;
   grid-template: auto 1fr / 1fr;
   background: var(--black-p);
   border-radius: 25px;
   padding: 15px;
   gap: 15px;
   position: relative;
   &__img {
      position: relative;
      width: 100%;
      aspect-ratio: 5 / 4;
   }
   &__img img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 15px;
   }
   &__info {
      color: var(--white-p);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 15px;
      padding: 5px;
   }
   &__info-top {
      display: flex;
      flex-direction: column;
      gap: 10px;
   }
   &__title {
      font-size: 25px;
   }
   &__descript {
      font-weight: 400;
   }
   &__date {
      font-style: italic;
      font-weight: 400;
      color: var(--white-s);
   }
   &__icon {
      position: absolute;
      bottom: 20px;
      right: 20px;
   }
}
.news-details {
   padding: 20px;
   &__body {
      display: flex;
      flex-direction: column;
      gap: 20px;
   }
   &__container {
      display: grid;
      grid-template-rows: repeat(1fr);
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      gap: 15px;
   }
   &__img {
      width: 100%;
      height: 100%;
      border-radius: 25px;
   }
}
